	module.exports = {"data":{"markdownRemark":{"html":"<h2>Dividing Fractions by Whole Numbers</h2>\n<p>Here are the steps to divide a fraction by a whole number</p>\n<ol>\n<li>Multiply the denominator of the fraction by the whole number. </li>\n<li>If possible, simplify the resultant fraction</li>\n</ol>\n<h4>Example 1</h4>\n<pre>  <span class=\"fraction\"><sup>4</sup>⁄<sub>5</sub></span> &divide; 3 = <span class=\"fraction\"><sup>4</sup>⁄<sub>(3 * 5)</sub></span> = <span class=\"fraction\"><sup>4</sup>⁄<sub>15</sub></span> </pre>\n<h4>Example 2</h4>\n<pre>  <span class=\"fraction\"><sup>6</sup>⁄<sub>7</sub></span> &divide; 2 = <span class=\"fraction\"><sup>6</sup>⁄<sub>(2 * 7)</sub></span> = <span class=\"fraction\"><sup>6</sup>⁄<sub>14</sub></span> = <span class=\"fraction\"><sup>3</sup>⁄<sub>7</sub></span> (Simplified) </pre>\n<p>Please note that a whole number can also be considered as a fraction by considering its denominator as 1. It, then becomes similar to dividing two fractions</p>","fields":{"slug":"/mathematics/dividing-fractions-by-whole-numbers/"},"frontmatter":{"title":"Dividing Fractions by Whole Numbers"}}},"pathContext":{"slug":"/mathematics/dividing-fractions-by-whole-numbers/"}}

